"use client";
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import axios from 'axios';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import Link from 'next/link';
import { UserPlus } from 'lucide-react';

const registerSchema = z.object({
    username: z.string().min(3, 'Username must be at least 3 characters'),
    email: z.string().email('Invalid email address'),
    password: z.string().min(6, 'Password must be at least 6 characters'),
    role: z.enum(['user', 'manager']),
});

type RegisterFormData = z.infer<typeof registerSchema>;

export default function RegisterPage() {
    const router = useRouter();
    const [error, setError] = useState('');
    const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm<RegisterFormData>({
        resolver: zodResolver(registerSchema),
        defaultValues: {
            role: 'user'
        }
    });

    const onSubmit = async (data: RegisterFormData) => {
        try {
            await axios.post('http://localhost:5000/api/auth/register', data);
            router.push('/login');
        } catch (err: any) {
            setError(err.response?.data?.error || 'Registration failed');
        }
    };

    return (
        <div className="flex min-h-screen items-center justify-center bg-gray-50 px-4 py-12 sm:px-6 lg:px-8">
            <div className="w-full max-w-md space-y-8 rounded-2xl bg-white p-10 shadow-xl ring-1 ring-gray-900/5 transition-all hover:shadow-2xl">
                <div className="text-center">
                    <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-green-100">
                        <UserPlus className="h-8 w-8 text-green-600" />
                    </div>
                    <h2 className="text-3xl font-extrabold tracking-tight text-gray-900">
                        Create an Account
                    </h2>
                    <p className="mt-2 text-sm text-gray-600">
                        Join your team and clear your tasks
                    </p>
                </div>

                <form className="mt-8 space-y-5" onSubmit={handleSubmit(onSubmit)}>
                    <div className="space-y-4">
                        <Input
                            label="Username"
                            type="text"
                            placeholder="johndoe"
                            {...register('username')}
                            error={errors.username?.message}
                        />
                        <Input
                            label="Email address"
                            type="email"
                            placeholder="you@example.com"
                            {...register('email')}
                            error={errors.email?.message}
                        />
                        <Input
                            label="Password"
                            type="password"
                            placeholder="••••••••"
                            {...register('password')}
                            error={errors.password?.message}
                        />

                        <div className="space-y-1.5">
                            <label className="block text-sm font-medium text-gray-700">Role</label>
                            <div className="relative">
                                <select
                                    {...register('role')}
                                    className="block w-full appearance-none rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm text-gray-900 shadow-sm transition-all focus:border-green-500 focus:outline-none focus:ring-4 focus:ring-green-500/10"
                                >
                                    <option value="user">Team Member (User)</option>
                                    <option value="manager">Project Manager</option>
                                </select>
                                <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-4 text-gray-500">
                                    <svg className="h-4 w-4 fill-current" viewBox="0 0 20 20">
                                        <path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
                                    </svg>
                                </div>
                            </div>
                            {errors.role && <p className="text-xs font-medium text-red-500">{errors.role.message}</p>}
                        </div>
                    </div>

                    {error && (
                        <div className="rounded-md bg-red-50 p-4 border border-red-200">
                            <p className="text-sm font-medium text-red-800 text-center">{error}</p>
                        </div>
                    )}

                    <div>
                        <Button
                            type="submit"
                            className="w-full bg-green-600 hover:bg-green-700 shadow-green-500/30 text-white"
                            size="lg"
                            isLoading={isSubmitting}
                        >
                            Start using TaskFlow
                        </Button>
                    </div>

                    <div className="text-center text-sm">
                        <span className="text-gray-500">Already a member? </span>
                        <Link href="/login" className="font-semibold text-green-600 hover:text-green-500 transition-colors">
                            Sign in
                        </Link>
                    </div>
                </form>
            </div>
        </div>
    );
}
